___CSS_LOADER_EXPORT___.push([module.id, `.list_container {
    width: 70%;
    min-width: 1000px;
    max-width: 1320px;
    margin: 0px auto 0px auto;
}

.wrapper_pagination {
    display: flex;
    justify-content: center;
}

.listItem_container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    /* padding-top: 15px; */
}

.wrapper_listItem {
    width: 250px;
    height: 320px;
    cursor: pointer;
    margin: 10px 2px;
    border: 3px solid black;
    border-radius: 5px;
    transition: 0.2s;
}

.wrapper_listItem:hover {
    box-shadow: 4px 4px 4px 0px #000000;
    scale: 1.05;
}

#listItem_img {
    width: 100%;
    height: 240px;
}

.wrapper_listItem > p {
    width: 100%;
    height: 30px;
    word-break: keep-all;
    font-weight: bolder;
    line-height: 30px;
    margin-left: 2px;
}

.empty_container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px;
}

.empty_container > img {
    width: 80px;
    height: 80px;
}

.empty_container > p {
    margin-left: 20px;
    font-size: 20px;
    font-weight: bolder;
}

.link_tag {
    color: black;
    text-decoration: none;
}
