___CSS_LOADER_EXPORT___.push([module.id, `.main_type_container {
    width: 100%;
    height: 35px;
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.main_type {
    width: 60%;
    min-width: 900px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    text-align: center;
}

.main_category {
    display: flex;
}

.main_category > li {
    font-weight: bolder;
    font-size: 1.2rem;
    margin-left: 15px;
    cursor: pointer;
}

.selected {
    border-bottom: 3px solid #394867;
}

.middle_type_container {
    width: 100%;
    height: 140px;
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.middle_type {
    width: 65%;
    min-width: 950px;
    height: 100%;
    display: flex;
    border-top: 4px solid grey;
}

.middle_category {
    display: flex;
    flex-wrap: wrap;
}

.middle_category > li {
    width: 65px;
    height: 45px;
    font-size: 1.1rem;
    margin: 10px 0px 10px 15px;
    word-break: keep-all;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.middle_category > li:hover {
    scale: 1.1;
}

.selectMonth {
    width: 60px;
    text-align: center;
    border: 2px solid black;
    border-radius: 10px;
    margin: 0px 20px 0px auto;
    cursor: pointer;
}
