import { useEffect, useState } from "react";
import Switch from "@mui/material/Switch";
import Axios from "axios";
import GuCategory from "./GuCategory";
import { useSelector, useDispatch } from "react-redux";
import {
    selectedGuname,
    checkedFree,
    selectedMonth,
} from "../../store/reducer/selectListSlice";

import "../../css/Category.css";

import handleSelectCategory from "../../js/handleSelectCategory";

function Category() {
    const dispatch = useDispatch();
    const [gulist, setGulist] = useState();

    const currentMonth = useSelector((state) => state.selectedList.month);

    // load guname list
    useEffect(() => {
        async function getGulist() {
            const { data } = await Axios.get("/api/guname");
            setGulist(data);
        }
        getGulist();

        dispatch(selectedGuname("ì „ì²´"));
        dispatch(checkedFree(0));

        //eslint-disable-next-line
    }, []);

    const handleCheckedFree = (event) => {
        if (event.target.checked) {
            dispatch(checkedFree(1));
        } else {
            dispatch(checkedFree(0));
        }
    };

    const handleSelectMonth = (e) => {
        dispatch(selectedMonth(e.target.value));
    };

    return (
        <>
            <div className="main_type_container">
                <div className="main_type">
                    <ul className="main_category">
                        <li className="selected" onClick={handleSelectCategory}>
                            ìì¹˜êµ¬
                        </li>
                        {/* <li>ì¶”í›„ ì¹´í…Œê³ ë¦¬ ì¶”ê°€ ê³ ë ¤</li> */}
                    </ul>
                    <select
                        className="selectMonth"
                        onChange={handleSelectMonth}
                        defaultValue={currentMonth}
                    >
                        <option value={"00"}>ì „ì²´</option>
                        <option value={"01"}>1ì›”</option>
                        <option value={"02"}>2ì›”</option>
                        <option value={"03"}>3ì›”</option>
                        <option value={"04"}>4ì›”</option>
                        <option value={"05"}>5ì›”</option>
                        <option value={"06"}>6ì›”</option>
                        <option value={"07"}>7ì›”</option>
                        <option value={"08"}>8ì›”</option>
                        <option value={"09"}>9ì›”</option>
                        <option value={"10"}>10ì›”</option>
                        <option value={"11"}>11ì›”</option>
                        <option value={"12"}>12ì›”</option>
                    </select>
                    <div>
                        <strong>ë¬´ë£Œ</strong>
                        <Switch
                            onChange={handleCheckedFree}
                            inputProps={{ "aria-label": "controlled" }}
                        />
                    </div>
                </div>
            </div>
            <GuCategory gulist={gulist} />
        </>
    );
}

export default Category;
