    width: 100%;
    height: 80px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-evenly;
}

.wrapper_logo {
    width: 200px;
    height: 80px;
}

.logo {
    width: 200px;
    height: 80px;
    display: flex;
    align-items: flex-end;
    text-decoration: none;
}

.logo > img {
    width: 50%;
    height: 100%;
}

.logo > p {
    font-size: 2rem;
    padding: 0px 0px 10px 10px;
    color: #c59a83;
    font-weight: bolder;
}

.wrapper_login {
    height: 100%;
    width: 200px;
    display: flex;
    align-items: flex-end;
    justify-content: right;
}
