___CSS_LOADER_EXPORT___.push([module.id, `.wrapper_banner {
    width: 100%;
    height: 325px;
}

.wrapper_bannerItem {
    width: 100%;
    height: 100%;
}

.wrapper_bannerContent {
    margin: 0 auto;
    width: 50%;
    min-width: 800px;
    height: 100%;
    display: flex;
}

.banner_description {
    width: 70%;
    height: 100%;
    font-size: 1.4rem;
    font-weight: bolder;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.banner_description > * {
    margin-left: 30px;
}

.banner_description > button {
    width: 200px;
    height: 30px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bolder;
}

.banner_description > button:hover {
    background-color: #c07f00;
}

.banner_img {
    width: 30%;
    height: 100%;
    background-color: black;
}

.banner_img > img {
    width: 100%;
    height: 100%;
}
